<template>
	<div id="kefu-index">
		<public-header :title="title">
			<router-link tag="div" :to="util.getUrl({path: '/pages/kefu/setting', query: {}})" slot="right" class="flex">
				<i class="icon icon-settings"></i>
			</router-link>
		</public-header>
		<div class="content font-14">
			<van-list
				v-model="chats.loading"
				:finished="chats.finished"
				@load="onLoad"
				:offset="100"
				:immediate-check="true"
				class="chat-list"
			>
				<template v-for="(item, index) in chats.data" >
					<van-swipe-cell :right-width="70">
						<router-link
							:to="util.getUrl({path: '/pages/kefu/chat', query: {relation: item.relation, fansopenid: item.fansopenid}})"
							class="chat-item"
							tag="div"
						>
							<div class="item-inner" :class="{'van-hairline--top': index > 0}">
								<div class="avatar">
									<img class="img-100" :src="item.fansavatar" alt="">
								</div>
								<div class="info">
									<div class="flex-lr">
										<div class="nickname ellipsis ">{{item.fansnickname}}</div>
										<div class="lasttime ellipsis c-gray">{{item.lasttime_cn}}</div>
									</div>
									<div class="last-content w-100 ellipsis c-gray font-13">{{item.content}}</div>
								</div>
								<div class="not-read" v-if="item.kefunotread > 0">{{item.kefunotread}}</div>
							</div>
						</router-link>
						<span slot="right" class="del-chat" @click.stop="onCloseChat(index)">删除</span>
					</van-swipe-cell>
				</template>
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		data() {
			return {
				title: '',
				chats: {
					page: 1,
					psize: 20,
					loading: false,
					finished: false,
					data: []
				},
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				var that = this;
				if(that.chats.finished) {
					return false;
				}
				that.util.request({
					url: 'manage/kefu/clerk/index',
					data: {
						page: that.chats.page,
						psize: that.chats.psize,
					}
				}).then((res) => {
					that.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						that.util.$toast(result.message)
						return;
					}
					result = result.message;
					that.title = '消息中心-【' + result.manager.kefu_status_cn + '】';
					var chats = result.chats.chats;
					if(chats && chats.length > 0) {
						that.chats.data = [...that.chats.data, ...chats];
						if(chats.length < that.chats.psize) {
							that.chats.finished = true;
						}
					} else {
						that.chats.finished = true;
					}
					that.chats.page++;
					that.chats.loading = false;
				})
			},
			onCloseChat(index) {
				var that = this;
				that.$dialog.confirm({
					title: '确定从会话列表移除该会话吗',
				}).then(() => {
					that.util.request({
						url: 'manage/kefu/clerk/delete',
						data: {
							chatid: that.chats.data[index].id
						}
					}).then((res) => {
						let result = res.data.message;
						if(result.errno) {
							that.util.$toast(result.message)
							return;
						}
						that.$delete(that.chats.data, index);
					})
				}).catch(() => {

				})
			}
		},
		mounted() {

		},
		components: {
			PublicHeader,
		},
	}
</script>

<style>
	#kefu-index .chat-list .chat-item{
		background-color: #fff;
		padding-left: 15px;
	}
	#kefu-index .chat-list .chat-item .item-inner{
		padding: 15px 15px 15px 0;
		display: flex;
		flex-flow: row wrap;
		align-items: center;
		justify-content: space-between;
		position: relative;
	}
	#kefu-index .chat-list .chat-item .item-inner .avatar{
		width: 40px;
		height: 40px;
		border-radius: 50%;
		overflow: hidden;
	}
	#kefu-index .chat-list .chat-item .item-inner .info{
		position: absolute;
		top: 15px;
		right: 15px;
		left: 55px;
		bottom: 15px;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
	}
	#kefu-index .chat-list .chat-item .item-inner .info .nickname{
		width: 70%;
	}
	#kefu-index .chat-list .chat-item .item-inner .info .lasttime{
		width: 30%;
		font-size: 12px;
		text-align: right;
	}
	#kefu-index .chat-list .chat-item .item-inner .not-read{
		position: absolute;
		z-index: 10;
		top: 12px;
		left: 30px;
		min-width: 12px;
		padding: 0 2px;
		border-radius: 50px;
		font-size: 12px;
		background-color: #ff2d4b;
		color: #fff;
	}
	#kefu-index .chat-list .del-chat{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 70px;
		height: 70px;
		background-color: #33aafc;
		color: #fff;
	}
</style>


